	onDragStart=(e,item)=>{
		// console.log(e)
		e.dataTransfer.dropEffect="all";
		e.dataTransfer.setData('item',JSON.stringify(item));

		// console.log(item)
	}

    render(){

        return (
			<div>
				  <div className="wf-panel-body">
                        {
                            this.state.components.map((i, x) => {
                                return <div className="wf-widgetsitem" draggable={true} onDragStart={(e)=>{this.onDragStart(e,i)}} data-index={x} data-type={i.componentName}  key={x}>
                                    <label>{i.name}</label>
                                    <i className={i.componentName}></i>
                                </div>
                            })
                        }
                    </div>
					
			</div>
		)
        
    }
