import React, { Component } from 'react';
import { withRouter } from "react-router-dom";
// import {findDOMNode} from 'react-dom'
import {Button,Input} from 'antd'
import './setting.less'
@withRouter
class Setting extends Component{
	constructor(){
		super();
		this.state={
			select:{}
		}
	}
	componentWillReceiveProps(props){
		// console.log(props)
		this.setState({
			...this.state,
			select:props.select
		})
	}
	changedefaultlabel=(e,item)=>{
		let value  = e.target.value;
		console.log(value);
		item.defaultlabel = value;
		console.log(item)
		this.props.changeComponent(item);
		this.setState({
			...this.state,
			select:{
				...this.state.select,
				defaultlabel:value,
			}
		})
	}
    render(){
		let select = this.state.select;
        return (
			<div style={{padding:"0 20px"}}>
				<p style={{textAlign:"center",color:"#fff",lineHeight:"30px"}}>组件属性</p>
				<p style={{textAlign:"center",color:"#fff",lineHeight:"30px"}}>---------------------------------------------</p>
				{ 
					select.name==undefined 
					?
					null
					:
					<div>
					<p className="setting_title" style={{textAlign:"center"}}>

						{
							select.name
						}
					</p>
					<p  className="setting_title">标题：</p>
					<Input value={select.defaultlabel}  onChange={(e)=>{this.changedefaultlabel(e,select)}} />
					{
						select.defaultlabel2
						?
						<Input value={select.defaultlabel} onChange={()=>{this.changedefaultlabel(select)}} />
						:
						null
					}
					</div>			
				}
				
			</div>
		)
        
    }
}

export default Setting;